const PACKAGE_NAME = '@wix/auto_sdk_online-programs_participants';

/**
 * Member joins the program. By the type of program, participant in corresponding
 * state will be created.
 */
export function joinProgram(payload: object): RequestOptionsFactory<any> {
  function __joinProgram({ host }: any) {
    const metadata = {
      entityFqdn: 'wix.online_programs.v3.participant',
      method: 'POST' as any,
      methodFqn:
        'wix.onlineprograms.participants.v3.ParticipantsService.JoinProgram',
      packageName: PACKAGE_NAME,
      migrationOptions: {
        optInTransformResponse: true,
      },
      url: resolveWixOnlineprogramsParticipantsV3ParticipantsServiceUrl({
        protoPath: '/v3/participants/join',
        data: payload,
        host,
      }),
      data: payload,
      transformResponse: (payload: any) =>
        transformPaths(payload, [
          {
            transformFn: transformRESTTimestampToSDKTimestamp,
            paths: [
              { path: 'participant.createdDate' },
              { path: 'participant.updatedDate' },
              { path: 'participant.certificate.issuedDate' },
            ],
          },
          {
            transformFn: transformRESTFloatToSDKFloat,
            paths: [{ path: 'participant.progress.completionPercentage' }],
          },
        ]),
    };

    return metadata;
  }

  return __joinProgram;
}

